import json from '@/Json';
import mixLoading from '@/components/mix-loading/mix-loading';
export default {
	components: {
		mixLoading
	},
	data() {
		return {
			loading: true,
			detailData: {},
			newsList: [],
			evaList: [],
		}
	},
	onLoad(options){
		console.log(options.data);
		this.detailData = JSON.parse(options.data);
		this.loadNewsList();
		this.loadEvaList();
	},
	methods: {
		//获取推荐列表
		async loadNewsList(){
			let list = await json.newsList;
			setTimeout(()=>{
				list.sort((a,b)=>{
					return Math.random() > .5 ? -1 : 1; //静态数据打乱顺序
				})
				list.forEach(item=>{
					if(item.images.length > 0){
						this.newsList.push(item);
					}
				})
				this.loading = false;
			}, 1000)
		},
		//获取评论列表
		async loadEvaList(){
			this.evaList = await json.evaList;
		},
		redirectToDetail(){
			uni.redirectTo({
				url: 'details'
			})
		}
	}
}
